@font-face {
    font-family: 'Bebas Neue';
    font-style: normal;
    font-weight: 400;
    src:
        local('BebasNeue'),
        local('BebasNeue-Regular'),
        url(../fonts/bebas_neue/BebasNeue-Regular.otf) format('opentype');
}

@font-face {
    font-family: 'Gotham Condensed';
    font-style: normal;
    font-weight: 400;
    src:
        local('GothamCondensed'),
        local('GothamCondensed-Medium'),
        url(../fonts/gotham-condensed/GothamCondensed-Medium.otf) format('opentype');
}


